body {
  background-color: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  overflow: hidden;
}

.digital-time {
  position: absolute;
  top: 50px;
  right: 50px;
  font-size: 2vw; /* ビューポートの幅に応じてサイズを調整 */
  color: white;
  text-align: right;
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px; /* 余白を追加 */
}

svg {
  max-width: 100%; /* 画面の100%を最大幅とする */
  max-height: 100%;
}


